<template>
  <div class="registered-main">
    <c-nav-bar class="registered-main-header">
      <span slot="left" class="nav-bar-left" @click="clickLeft">
        <i class="iconfont icon-fanhui1"></i>
      </span>
    </c-nav-bar>
    <div class="registered-content">
      <div class="registered-title">
        <span>找回密码</span>
      </div>
      <ul class="turn-registered-content">
        <li>
          <input type="text" v-model="findPass.mobile" placeholder="输入手机号">
        </li>
        <li class="get-verCode">
          <input :disabled="verDisabled" v-model="findPass.authCode" type="text" placeholder="输入手机验证码">
          <c-countdown :getVerUrl="getVerUrl" :phoneCode="findPass.mobile" :totalTime="totalTime" @verCodeFlag="verCodeFlag"></c-countdown>
        </li>
        <li>
          <input type="text" v-model="findPass.setPassword" placeholder="设置新密码">
        </li>
        <li>
          <input type="text" @blur="isSamePass" v-model="findPass.password" placeholder="确定新密码">
        </li>
      </ul>
      <div class="registered-commit">
        <van-button round type="default" :disabled="findPassBtnFlag" :class="[findPassBtnFlag? 'bg-gray':'bg-orange-dark']" class="commit-registered" @click="commitFindPass">
          确定
        </van-button>
      </div>
    </div>
  </div>
</template>
<script>
import { checkMobile,isAllNumber,checkPass } from '@/js/checkUtil'
export default {
  name: 'findPass',
  data() {
    return {
      verDisabled: true,
      findPassBtnFlag: true,
      totalTime: 60,
      getVerUrl: '/api/authCode/sendForForgotPassword',
      findPass: {
        mobile: '',
        authCode: '',
        password: '',
        setPassword: ''
      }
    }
  },
  watch: {
    findPass: {
      handler(){
        let flag;
        for ( let key in this.findPass ) {
          if (!this.findPass[key]){
            flag=true;
            break
          }
        }
        this.findPassBtnFlag=flag||false;
      },
      deep: true
    }
  },
  methods: {
    clickLeft() {
      this.$router.push({name: '登录'})
    },
    //获取验证码
    verCodeFlag(childValue) {
      this.verDisabled = childValue
    },
    //判断设置的密码是否一致
    isSamePass() {
      if (this.findPass.password !== this.findPass.setPassword) {
        this.$toast('两次密码不一致，请重新输入！');
        this.findPass.password='';
      }
    },
    commitFindPass() {
      if (!checkMobile(this.findPass.mobile)) return;
      if (!checkPass(this.findPass.setPassWord,'密码')) return;
      if (!isAllNumber(this.findPass.authCode)) {
        this.$toast('请输入正确的验证码!');
        return
      }
      this.$http.post('/api/user/forgotPassword',this.findPass).then(response => {
        console.log(response)
      },response => {
        console.log(response)
      })
    }
  }
}
</script>
<style lang="scss" scoped>
  .bg-orange-dark {
    background: linear-gradient(left bottom, $orange-dark, $orange)
  }
  .bg-gray{
    background: $gray;
  }
  .registered-main {
    .registered-main-header {
      background: #fff;
    }
    .registered-content {
      width: 100%;
      top: 44px;
      position: absolute;
      bottom: 0;
      background: $white;
      overflow: hidden;
      padding: 8%;
      display: flex;
      flex-direction: column;
      .registered-title {
        height: 100px;
        line-height: 100px;
        font-size: 25px;
        color: $orange-dark;
        span {
          font-size: 25px;
          font-weight: bold;
          color: #000;
          margin-left: 2%;
        }
      }
      .registered-commit {
        width: 100%;
        margin-top: 40px;
        .commit-registered {
          width: 100%;
          color: $white;
        }
        .agree-registered {
          width: 100%;
          line-height: 40px;
          text-align: center;
          font-size: 15px;
          i {
            color: $orange-dark;
          }
        }
      }
      .turn-registered-content {
        li {
          width: 100%;
          height: 50px;
          display: flex;
          align-items: center;
          border-bottom: 1px solid $gray;
          input {
            width: 78%;
            height: 20px;
          }
        }
        .get-verCode {
          display: flex;
          align-items: center;
          input {
            flex: 1;
          }
          span {
            color: $orange-dark;
            font-size: 15px;
          }
        }
      }
    }
  }
</style>
